.speech-bubble {
    filter: drop-shadow(-1px -1px 2px rgba(0, 0, 0, 0.1))
      drop-shadow(1px 2px 2px rgba(0, 0, 0, 0.15));
    max-width: 60%;
    margin-left: 10px;
    padding: 1rem 2rem;
    position: relative;
    font-family: "Source Sans Pro", sans-serif;
    font-size: 1.2rem;
    font-weight: 400;
    background: #8ca8d8;
    color: white;
  }
  
  .speech-bubble p {
    text-align: left;
    margin-top: 5px;
    margin-bottom: 10px;
  }
  
  .speech-bubble cite {
    position: absolute;
    bottom: 0.5rem;
    left: 1rem;
    font-size: 0.8rem;
    font-style: italic;
    font-weight: 600;
    letter-spacing: 0.5px;
    color: white;
    max-width: 100px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

 